"use client"

import { useState } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { getDayName } from "@/lib/data"
import { cn } from "@/lib/utils"

const EMOJI_OPTIONS = ["üßò", "üí™", "üìö", "üíß", "‚úçÔ∏è", "üìµ", "üèÉ", "üéØ", "üß†", "üò¥", "ü•ó", "üé®"]
const COLOR_OPTIONS = ["#0A5F4E", "#E85D4C", "#5B8DEF", "#4ECDC4", "#9B59B6", "#F39C12"]

interface AddHabitModalProps {
  open: boolean
  onClose: () => void
  onSave: (habit: {
    name: string
    emoji: string
    color: string
    reminderTime: string
    repeatDays: number[]
  }) => void
  initialData?: {
    name: string
    emoji: string
    color: string
    reminderTime: string
    repeatDays: number[]
  }
}

export function AddHabitModal({ open, onClose, onSave, initialData }: AddHabitModalProps) {
  const [name, setName] = useState(initialData?.name || "")
  const [emoji, setEmoji] = useState(initialData?.emoji || "üéØ")
  const [color, setColor] = useState(initialData?.color || "#0A5F4E")
  const [reminderTime, setReminderTime] = useState(initialData?.reminderTime || "")
  const [repeatDays, setRepeatDays] = useState<number[]>(initialData?.repeatDays || [0, 1, 2, 3, 4, 5, 6])

  const toggleDay = (day: number) => {
    if (repeatDays.includes(day)) {
      setRepeatDays(repeatDays.filter((d) => d !== day))
    } else {
      setRepeatDays([...repeatDays, day].sort())
    }
  }

  const handleSave = () => {
    if (!name.trim()) return
    onSave({ name, emoji, color, reminderTime, repeatDays })
    setName("")
    setEmoji("üéØ")
    setColor("#0A5F4E")
    setReminderTime("")
    setRepeatDays([0, 1, 2, 3, 4, 5, 6])
    onClose()
  }

  return (
    <Dialog open={open} onOpenChange={onClose}>
      <DialogContent className="glass max-w-md border-0">
        <DialogHeader>
          <DialogTitle className="text-xl font-bold">{initialData ? "Edit Habit" : "Add New Habit"}</DialogTitle>
        </DialogHeader>

        <div className="space-y-6 py-4">
          <div className="space-y-2">
            <Label>Habit Name</Label>
            <Input
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="e.g., Meditate for 10 minutes"
              className="border-border/50 bg-background/50"
            />
          </div>

          <div className="space-y-2">
            <Label>Choose an Emoji</Label>
            <div className="flex flex-wrap gap-2">
              {EMOJI_OPTIONS.map((e) => (
                <button
                  key={e}
                  onClick={() => setEmoji(e)}
                  className={cn(
                    "flex h-12 w-12 items-center justify-center rounded-xl text-2xl transition-all",
                    emoji === e ? "bg-primary/20 ring-2 ring-primary" : "bg-secondary hover:bg-secondary/80",
                  )}
                >
                  {e}
                </button>
              ))}
            </div>
          </div>

          <div className="space-y-2">
            <Label>Choose a Color</Label>
            <div className="flex gap-2">
              {COLOR_OPTIONS.map((c) => (
                <button
                  key={c}
                  onClick={() => setColor(c)}
                  className={cn(
                    "h-10 w-10 rounded-full transition-all",
                    color === c && "ring-2 ring-offset-2 ring-foreground",
                  )}
                  style={{ backgroundColor: c }}
                />
              ))}
            </div>
          </div>

          <div className="space-y-2">
            <Label>Reminder Time (optional)</Label>
            <Input
              type="time"
              value={reminderTime}
              onChange={(e) => setReminderTime(e.target.value)}
              className="border-border/50 bg-background/50"
            />
          </div>

          <div className="space-y-2">
            <Label>Repeat Days</Label>
            <div className="flex gap-1">
              {[0, 1, 2, 3, 4, 5, 6].map((day) => (
                <button
                  key={day}
                  onClick={() => toggleDay(day)}
                  className={cn(
                    "flex h-10 w-10 items-center justify-center rounded-lg text-sm font-medium transition-all",
                    repeatDays.includes(day)
                      ? "bg-primary text-primary-foreground"
                      : "bg-secondary text-secondary-foreground hover:bg-secondary/80",
                  )}
                >
                  {getDayName(day)}
                </button>
              ))}
            </div>
          </div>
        </div>

        <div className="flex gap-3">
          <Button variant="outline" onClick={onClose} className="flex-1 bg-transparent">
            Cancel
          </Button>
          <Button onClick={handleSave} className="flex-1 bg-primary text-primary-foreground">
            {initialData ? "Save Changes" : "Add Habit"}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  )
}
