"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { getTodayQuote, microArticles } from "@/lib/data"
import type { MicroArticle } from "@/lib/types"
import { cn } from "@/lib/utils"

export function VybesTab() {
  const quote = getTodayQuote()
  const [expandedArticle, setExpandedArticle] = useState<string | null>(null)

  const categories = [...new Set(microArticles.map((a) => a.category))]
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null)

  const filteredArticles = selectedCategory
    ? microArticles.filter((a) => a.category === selectedCategory)
    : microArticles

  return (
    <div className="flex flex-col pb-24 px-5 pt-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
      <h1 className="text-3xl font-bold text-foreground">Vybes</h1>
      <p className="text-muted-foreground mt-1">Learn, grow, stay motivated</p>

      {/* Daily Quote Hero */}
      <Card className="glass mt-6 border-0 p-6 shadow-lg bg-primary/5">
        <div className="text-4xl mb-4">&ldquo;</div>
        <p className="text-xl font-medium leading-relaxed text-foreground text-balance">{quote.text}</p>
        <p className="mt-4 text-sm text-muted-foreground">‚Äî {quote.author}</p>
      </Card>

      {/* Category Filters */}
      <div className="flex gap-2 mt-6 overflow-x-auto pb-2 -mx-5 px-5">
        <button
          onClick={() => setSelectedCategory(null)}
          className={cn(
            "shrink-0 px-4 py-2 rounded-full text-sm font-medium transition-all",
            selectedCategory === null ? "bg-primary text-primary-foreground" : "bg-secondary text-secondary-foreground",
          )}
        >
          All
        </button>
        {categories.map((cat) => (
          <button
            key={cat}
            onClick={() => setSelectedCategory(cat)}
            className={cn(
              "shrink-0 px-4 py-2 rounded-full text-sm font-medium transition-all whitespace-nowrap",
              selectedCategory === cat
                ? "bg-primary text-primary-foreground"
                : "bg-secondary text-secondary-foreground",
            )}
          >
            {cat}
          </button>
        ))}
      </div>

      {/* Micro Articles */}
      <div className="mt-4 space-y-3">
        <h2 className="text-lg font-semibold text-foreground">1-Minute Reads</h2>

        {filteredArticles.map((article: MicroArticle) => (
          <Card
            key={article.id}
            className={cn(
              "glass border-0 overflow-hidden shadow-lg cursor-pointer transition-all stagger-item",
              expandedArticle === article.id && "ring-2 ring-primary",
            )}
            onClick={() => setExpandedArticle(expandedArticle === article.id ? null : article.id)}
          >
            <div className="p-4">
              <div className="flex items-start gap-3">
                <span className="text-3xl">{article.emoji}</span>
                <div className="flex-1">
                  <div className="flex items-center justify-between">
                    <h3 className="font-semibold text-foreground">{article.title}</h3>
                    <span className="text-xs text-muted-foreground">{article.readTime}</span>
                  </div>
                  <span className="text-xs text-primary font-medium">{article.category}</span>
                </div>
              </div>

              {expandedArticle === article.id && (
                <p className="mt-4 text-sm leading-relaxed text-foreground/90 animate-in fade-in slide-in-from-top-2">
                  {article.content}
                </p>
              )}
            </div>
          </Card>
        ))}
      </div>
    </div>
  )
}
