"use client"

import { useState, useEffect } from "react"
import { HabitCard } from "@/components/habit-card"
import type { Habit } from "@/lib/types"
import { getTodayQuote, formatDate } from "@/lib/data"
import { getHabits, toggleCompletion, isCompleted, getStreak } from "@/lib/store"

export function HomeTab() {
  const [habits, setHabits] = useState<Habit[]>([])
  const [refreshKey, setRefreshKey] = useState(0)

  const today = new Date()
  const todayStr = formatDate(today)
  const dayOfWeek = today.getDay()
  const quote = getTodayQuote()

  useEffect(() => {
    const loadedHabits = getHabits().filter((h) => !h.archived && h.repeatDays.includes(dayOfWeek))
    setHabits(loadedHabits)
  }, [dayOfWeek, refreshKey])

  const handleToggle = (habitId: string) => {
    toggleCompletion(habitId, todayStr)
    setRefreshKey((k) => k + 1)
  }

  const completedCount = habits.filter((h) => isCompleted(h.id, todayStr)).length
  const progressPercent = habits.length > 0 ? (completedCount / habits.length) * 100 : 0

  return (
    <div className="flex flex-col pb-24 animate-in fade-in slide-in-from-bottom-4 duration-500">
      {/* Header */}
      <div className="px-5 pt-6 pb-4">
        <p className="text-sm font-medium text-muted-foreground">
          {today.toLocaleDateString("en-US", { weekday: "long", month: "long", day: "numeric" })}
        </p>
        <h1 className="text-3xl font-bold text-foreground mt-1">Today</h1>

        {/* Quote */}
        <div className="mt-4 rounded-2xl bg-primary/10 p-4">
          <p className="text-balance text-sm font-medium leading-relaxed text-foreground">&ldquo;{quote.text}&rdquo;</p>
          <p className="mt-2 text-xs text-muted-foreground">‚Äî {quote.author}</p>
        </div>

        {/* Progress */}
        {habits.length > 0 && (
          <div className="mt-4">
            <div className="flex items-center justify-between mb-2">
              <span className="text-sm text-muted-foreground">Today&apos;s Progress</span>
              <span className="text-sm font-semibold text-foreground">
                {completedCount}/{habits.length}
              </span>
            </div>
            <div className="h-2 rounded-full bg-secondary overflow-hidden">
              <div
                className="h-full rounded-full bg-success transition-all duration-500"
                style={{ width: `${progressPercent}%` }}
              />
            </div>
          </div>
        )}
      </div>

      {/* Habits List */}
      <div className="flex-1 px-5 space-y-3">
        {habits.length === 0 ? (
          <div className="flex flex-col items-center justify-center py-16 text-center">
            <div className="text-6xl mb-4">üå±</div>
            <h3 className="text-xl font-semibold text-foreground">No habits for today</h3>
            <p className="text-muted-foreground mt-2">Tap the + button to add your first habit</p>
          </div>
        ) : (
          habits.map((habit) => (
            <HabitCard
              key={habit.id}
              habit={habit}
              isCompleted={isCompleted(habit.id, todayStr)}
              streak={getStreak(habit.id)}
              onToggle={() => handleToggle(habit.id)}
            />
          ))
        )}
      </div>
    </div>
  )
}
