"use client"

import { useState, useEffect, useMemo } from "react"
import { ChevronLeft, ChevronRight } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { getHabits, getCompletions, getCompletionsForDate } from "@/lib/store"
import { formatDate } from "@/lib/data"
import type { Habit, HabitCompletion } from "@/lib/types"
import { cn } from "@/lib/utils"

export function CalendarTab() {
  const [currentDate, setCurrentDate] = useState(new Date())
  const [habits, setHabits] = useState<Habit[]>([])
  const [completions, setCompletions] = useState<HabitCompletion[]>([])
  const [selectedDate, setSelectedDate] = useState<string | null>(null)

  useEffect(() => {
    setHabits(getHabits().filter((h) => !h.archived))
    setCompletions(getCompletions())
  }, [])

  const year = currentDate.getFullYear()
  const month = currentDate.getMonth()

  const daysInMonth = new Date(year, month + 1, 0).getDate()
  const firstDayOfMonth = new Date(year, month, 1).getDay()

  const monthName = currentDate.toLocaleDateString("en-US", { month: "long", year: "numeric" })

  const completionMap = useMemo(() => {
    const map: Record<string, number> = {}
    completions.forEach((c) => {
      map[c.date] = (map[c.date] || 0) + 1
    })
    return map
  }, [completions])

  const getCompletionLevel = (dateStr: string): number => {
    const count = completionMap[dateStr] || 0
    const habitsForDate = habits.filter((h) => {
      const date = new Date(dateStr)
      return h.repeatDays.includes(date.getDay())
    }).length

    if (habitsForDate === 0) return 0
    const percent = count / habitsForDate
    if (percent === 0) return 0
    if (percent < 0.5) return 1
    if (percent < 1) return 2
    return 3
  }

  const prevMonth = () => {
    setCurrentDate(new Date(year, month - 1, 1))
  }

  const nextMonth = () => {
    setCurrentDate(new Date(year, month + 1, 1))
  }

  const selectedDateCompletions = selectedDate ? getCompletionsForDate(selectedDate) : []
  const selectedDateHabits = habits.filter((h) => {
    const completedIds = selectedDateCompletions.map((c) => c.habitId)
    return completedIds.includes(h.id)
  })

  return (
    <div className="flex flex-col pb-24 px-5 pt-6 animate-in fade-in slide-in-from-bottom-4 duration-500">
      <h1 className="text-3xl font-bold text-foreground">Calendar</h1>
      <p className="text-muted-foreground mt-1">Track your consistency</p>

      {/* Month Navigation */}
      <div className="flex items-center justify-between mt-6 mb-4">
        <Button variant="ghost" size="icon" onClick={prevMonth}>
          <ChevronLeft className="h-5 w-5" />
        </Button>
        <h2 className="text-lg font-semibold">{monthName}</h2>
        <Button variant="ghost" size="icon" onClick={nextMonth}>
          <ChevronRight className="h-5 w-5" />
        </Button>
      </div>

      {/* Calendar Grid */}
      <Card className="glass border-0 p-4 shadow-lg stagger-item">
        {/* Day Headers */}
        <div className="grid grid-cols-7 gap-1 mb-2">
          {["S", "M", "T", "W", "T", "F", "S"].map((day, i) => (
            <div key={i} className="text-center text-xs font-medium text-muted-foreground py-2">
              {day}
            </div>
          ))}
        </div>

        {/* Days */}
        <div className="grid grid-cols-7 gap-1">
          {/* Empty cells for days before first of month */}
          {Array.from({ length: firstDayOfMonth }).map((_, i) => (
            <div key={`empty-${i}`} className="aspect-square" />
          ))}

          {/* Days of month */}
          {Array.from({ length: daysInMonth }).map((_, i) => {
            const day = i + 1
            const dateStr = formatDate(new Date(year, month, day))
            const level = getCompletionLevel(dateStr)
            const isToday = dateStr === formatDate(new Date())
            const isSelected = dateStr === selectedDate

            return (
              <button
                key={day}
                onClick={() => setSelectedDate(dateStr)}
                className={cn(
                  "aspect-square flex items-center justify-center rounded-lg text-sm font-medium transition-all",
                  level === 0 && "bg-secondary/50 text-muted-foreground",
                  level === 1 && "bg-success/30 text-foreground",
                  level === 2 && "bg-success/60 text-foreground",
                  level === 3 && "bg-success text-success-foreground glow-complete",
                  isToday && "ring-2 ring-primary",
                  isSelected && "ring-2 ring-accent",
                )}
              >
                {day}
              </button>
            )
          })}
        </div>

        {/* Legend */}
        <div className="flex items-center justify-center gap-2 mt-4 text-xs text-muted-foreground">
          <span>Less</span>
          <div className="h-3 w-3 rounded bg-secondary/50" />
          <div className="h-3 w-3 rounded bg-success/30" />
          <div className="h-3 w-3 rounded bg-success/60" />
          <div className="h-3 w-3 rounded bg-success" />
          <span>More</span>
        </div>
      </Card>

      {/* Selected Date Details */}
      {selectedDate && (
        <Card className="glass border-0 mt-4 p-4 shadow-lg stagger-item animate-in fade-in duration-300">
          <h3 className="font-semibold text-foreground">
            {new Date(selectedDate).toLocaleDateString("en-US", {
              weekday: "long",
              month: "long",
              day: "numeric",
            })}
          </h3>
          {selectedDateHabits.length > 0 ? (
            <div className="mt-3 space-y-2">
              {selectedDateHabits.map((habit) => (
                <div key={habit.id} className="flex items-center gap-2">
                  <span className="text-lg">{habit.emoji}</span>
                  <span className="text-sm text-foreground">{habit.name}</span>
                  <span className="text-success ml-auto">‚úì</span>
                </div>
              ))}
            </div>
          ) : (
            <p className="text-sm text-muted-foreground mt-2">No habits completed</p>
          )}
        </Card>
      )}
    </div>
  )
}
